@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
}

body {
  color: #212529;
  font-family: Poppins, sans-serif;
}

::selection {
  color: #f8f9fa;
  background-color: #3342ce;
}

body, header {
  background-color: #f8f9fa;
}

.btn, .navbar-links li a, a, a:active {
  text-decoration: none;
}

#contact ul, .navbar-links li {
  list-style: none;
}

#about, footer {
  color: #f8f9fa;
}

li, p {
  font-size: 1.6rem;
  font-weight: 400;
}

a {
  color: #212529;
  font-weight: 500;
  transition: all .3s;
}

.navbar-links li a:hover, a:hover {
  color: #3342ce;
}

a.active {
  color: #3342ce !important;
}

img {
  object-fit: cover;
  border-radius: 10px;
  max-width: 100%;
  max-height: 100%;
}

.btn {
  background-color: #3342ce;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 1rem 5rem;
  font-size: 16px;
  transition: all .1s;
  display: flex;
  color: #f8f9fa !important;
}

.btn:hover {
  background-color: #2e3bb9;
  transform: scale(1.05);
}

.btn-secondary {
  background: none;
  border: 3px solid #3342ce;
  color: #3342ce !important;
}

#contact, .project {
  background-color: #e9ecef;
}

.btn-secondary:hover {
  background-color: #3342ce;
  color: #f8f9fa !important;
}

#about, .toggle-button .bar, footer {
  background-color: #212529;
}

.hidden {
  display: none !important;
}

.loading-container {
  z-index: 2;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
  overflow-x: hidden;
}

.loading {
  border: 8px solid #2e3bb975;
  border-top-color: #3342ce;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  animation: 1s ease-in-out infinite spin;
  display: inline-block;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.percContentToScroll, .percOfScrolled {
  height: 5px;
  position: fixed;
  top: 0;
}

.percContentToScroll {
  z-index: 2;
  background-color: #3343ce4d;
  width: 100%;
}

.percOfScrolled {
  z-index: 3;
  background-color: #3342ce;
  width: 0%;
}

header {
  z-index: 1;
  width: 100%;
  padding: 1.5rem 15rem;
  position: fixed;
  top: 0;
  box-shadow: 1px 1px #0000001a;
}

.navbar {
  color: #212529;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.logo {
  font-size: 3.2rem !important;
  font-weight: 700 !important;
}

.navbar-links ul {
  align-items: center;
  gap: 2rem;
  display: flex;
}

.navbar-links li a {
  color: #212529;
}

.navbar-links .btn {
  padding: 1rem 2rem;
}

.toggle-button {
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
  display: none;
  position: absolute;
  top: 1.2rem;
  right: 1rem;
}

#home, #projects {
  flex-direction: column;
  display: flex;
}

.toggle-button .bar {
  border-radius: 10px;
  width: 100%;
  height: 3px;
}

#home {
  background-image: url("wave.ae4f6efd.svg");
  background-position: bottom;
  background-size: cover;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.hero-title {
  text-align: center;
  max-width: 70vw;
  font-size: 6.4rem;
  line-height: 1.1;
}

.hero-content {
  text-align: center;
  margin-top: 2rem;
  font-weight: 400;
}

.hero-buttons {
  gap: 1.5rem;
  margin-top: 3rem;
  display: flex;
}

#about {
  grid-template-columns: 30rem 1fr;
  justify-content: center;
  gap: 7rem;
  padding: 7rem 30rem 10rem;
  display: grid;
}

.section-title {
  font-size: 4.8rem;
}

.about-texts {
  word-wrap: wrap;
  flex-direction: column;
  gap: 1rem;
  display: flex;
  overflow: hidden;
}

#skills {
  background-image: url("blob.1d2b26be.svg");
  background-position: top;
  background-size: cover;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 7rem 20rem 0;
  display: flex;
}

.skills {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  max-width: 90rem;
  display: flex;
}

.skill-badge {
  color: #3342ce;
  cursor: default;
  background-color: #0000;
  border: 3px solid #3342ce;
  border-radius: 10px;
  padding: .7rem;
  transition: all .3s;
}

.skill-badge:hover {
  transform: rotate(10deg);
}

#projects {
  background-image: url("blob2.719cf28b.svg");
  background-position: 0;
  background-size: cover;
  align-items: center;
  gap: 3rem;
  padding: 7rem 20rem;
  display: flex;
}

.project-cards {
  grid-template-columns: 1fr;
  gap: 2rem;
  display: grid;
}

#contact ul, .project-buttons {
  gap: 1rem;
  display: flex;
}

.project {
  border-radius: 5px;
  column-gap: 3rem;
  width: 75vw;
  padding: 3rem;
  transition: all .3s;
  display: grid;
  box-shadow: 2px 2px 1px #0000001a;
}

.project:nth-of-type(odd) {
  grid-template-columns: 40rem 1fr;
}

.project:nth-of-type(2n) {
  grid-template-columns: 1fr 40rem;
}

.project:nth-of-type(2n) .project-buttons {
  justify-content: flex-start;
}

.project:hover {
  transform: translateY(-5px);
}

.project img {
  height: 250px;
}

.project-details {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.project-title {
  font-size: 3.2rem;
}

.project-skills {
  flex-wrap: wrap;
  gap: .5rem;
  display: flex;
}

.project-buttons {
  justify-content: end;
  margin-top: 1rem;
}

#contact {
  grid-template-columns: 1fr 30rem;
  gap: 10rem;
  padding: 5rem 20rem;
  display: grid;
}

#contact ul {
  flex-direction: column;
  align-items: flex-start;
}

#contact a {
  color: #2e3bb9;
  justify-content: center;
  display: flex;
}

.list-icon {
  fill: #2e3bb9;
  width: 24px;
  margin-right: .5rem;
}

#contact a:hover, .list-icon:hover {
  fill: #2935a5;
  color: #2935a5;
}

footer {
  text-align: center;
  padding: 2rem;
}

@media only screen and (width <= 1300px) {
  header {
    padding: 1rem 5rem;
  }

  #home {
    padding: 10rem 2rem;
  }

  .hero-title {
    margin-bottom: 3rem;
    line-height: 1.2;
  }

  #about {
    gap: 3rem;
    padding: 5rem 3rem;
  }

  #projects {
    padding: 5rem 2rem;
  }

  .project:nth-of-type(odd) {
    flex-direction: column;
    display: flex;
  }

  .project:nth-of-type(2n) {
    flex-direction: column-reverse;
    display: flex;
  }

  .project img {
    margin-bottom: 2rem;
  }

  .project-buttons {
    text-align: center;
    flex-direction: column;
  }

  #contact {
    grid-template-columns: repeat(2, auto);
    gap: 2rem;
    padding: 3rem 10rem;
  }
}

@media only screen and (width <= 768px) {
  .navbar-links ul li, .project-buttons {
    text-align: center;
  }

  #about, #contact, .project-cards {
    grid-template-columns: 1fr;
  }

  header {
    background-color: #212529;
