  }

  a.active {
    color: #f8f9fa !important;
  }

  #home {
    min-height: auto;
    padding: 10rem 2rem;
  }

  .hero-title {
    max-width: 100%;
    margin-bottom: 3rem;
    font-size: 4.8rem;
    line-height: 1.2;
  }

  .hero-content {
    margin-top: 0;
  }

  .hero-buttons {
